import React from 'react'
import mainLogo from './images/rewdigitalMainLogo.svg'

import {Link} from "react-router-dom"
function Footer() {
  return (<>
  
  <section id="get-start" class="get-start-section relative-position">
			<div class="jarallax">
				<div class="overlay-2"></div>
				<div class="container">
					<div class="get-start-content">
						<div class="row section-padding"> 
							<div class="get-start-content text-center">
								<div class="section-title">
									<span class="sintony section-title-text  white">Dont wait any more</span>
									<h2 class="section-title-head white bold pb40  text-capitalize">Make explainer
									videos with innovation</h2>
								</div>

								<div class="browse-btn text-center white transation global-box-shadow">
									<Link to="#">Get Started</Link>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</section>

  <div className="footer-dark">
        <footer>
            <div className="container">
                <div className="row">
                    <div className="col-sm-6 col-md-3 item">
                        <h3>Work</h3>
                        <ul>
                            <li><Link to="#">Business</Link></li>
                            <li><Link to="#">Real estate</Link></li>
                            <li><Link to="#">E-commerce</Link></li>
                            <li><Link to="#">Vedio-adds</Link></li>
                            <li><Link to="#">Tutoril vedio</Link></li>
                            <li><Link to="#">Marketing vedio</Link></li>
                          
                            <li><Link to="#">Logo WaterMark</Link></li>

                        </ul>
                    </div>
                    <div className="col-sm-6 col-md-3 item">
                        <h3>Personal</h3>
                        <ul>
                            <li><Link to="#">Birthday</Link></li>
                            <li><Link to="#">Wedding</Link></li>
                            <li><Link to="#">Memorial</Link></li>
                            <li><Link to="#">Graduations</Link></li>
                            <li><Link to="#">Holiday</Link></li>
                            <li><Link to="#">Success Story</Link></li>
                        </ul>
                    </div>
                  
                    <div className="col-md-6  col-md-3 item text">
                        <h3>  RewDigital</h3>
                        <p>Boost your bussniess with vedio.
                          Rewdigital makes it easy to create marketing videos that get brand noticed 
                        </p>
                        <h3>About</h3>
                        <ul>
                            <li><Link to="#">Team</Link></li>
                            <li><Link to="#">Mission</Link></li>
                        </ul>
                    </div>
                    <div className="col item social"><Link to="#"><i className="icon ion-social-facebook"></i></Link><Link to="#"><i className="icon ion-social-twitter"></i></Link><Link to="#"><i className="icon ion-social-snapchat"></i></Link><Link to="#"><i className="icon ion-social-instagram"></i></Link>
                    </div>
                </div>
                <hr/>
  
    <img style={{width:"100px",height:"auto"}}  src={mainLogo} alt="logo" /> &copy; 2021 all rights reserved. 

    <hr />
            </div>
        </footer>
    </div>

  
  </>)
}

export default Footer
