import React from 'react'
import { Navbar,Container ,Nav,NavDropdown } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import mainLogo from './images/rewdigitalMainLogo.svg'
import rewdigital_toggle_buttoon from "./images/rewdigital_toggle_buttoon.svg"
function TopNavBar() {
    return (<>

<Navbar collapseOnSelect expand="lg" fixed="top" bg="light" variant="light">
  <Container>
  <Navbar.Brand href="/"><img style={{width:"150px",height:"auto"}} src={mainLogo} alt="logo" srcset="" /></Navbar.Brand>
  
<Navbar.Toggle aria-controls="basic-navbar-nav"><img style={{width:"39px",height:"auto"}} src={rewdigital_toggle_buttoon} alt="logo" srcset="" /> </Navbar.Toggle>
  <Navbar.Collapse id="responsive-navbar-nav">
  <Nav className="me-auto">

     

    </Nav>
    
    <Nav>
      <Nav.Link  href="#/">Home</Nav.Link>
      <Nav.Link href="#/Features">Features</Nav.Link>
      <Nav.Link href="#/Pricing">Pricing</Nav.Link>
  
      <NavDropdown title="more" id="collasible-nav-dropdown">
        <NavDropdown.Item href="#/About">About</NavDropdown.Item>
        <NavDropdown.Item href="#/Contact">Contact</NavDropdown.Item>
        <NavDropdown.Item href="#/Help">Help</NavDropdown.Item>
        <NavDropdown.Divider />
        <NavDropdown.Item href="#/Login">Login</NavDropdown.Item>
      </NavDropdown>
     
    </Nav>
  </Navbar.Collapse>
  </Container>
</Navbar>
   </> )
}

export default TopNavBar
